import React, { useState } from 'react';

const GalleryPage = () => {
  //  
  const [filter, setFilter] = useState('all');

  //   ( API     )
  const galleryItems = [
    { 
      id: 1, 
      title: '2025 3  ', 
      date: '2025-03-25', 
      category: 'regular',
      imageUrl: '/api/placeholder/400/300', //    
      description: ' CC  3  ' 
    },
    { 
      id: 2, 
      title: '2025 ', 
      date: '2025-05-27', 
      category: 'special',
      imageUrl: '/api/placeholder/400/300', //    
      description: '35   ' 
    },
    { 
      id: 3, 
      title: '2024 ', 
      date: '2024-10-22', 
      category: 'special',
      imageUrl: '/api/placeholder/400/300', //    
      description: '34   ' 
    },
    { 
      id: 4, 
      title: '2024 11  ', 
      date: '2024-11-26', 
      category: 'regular',
      imageUrl: '/api/placeholder/400/300', //    
      description: ' CC  11  ' 
    },
    { 
      id: 5, 
      title: '2024 9  ', 
      date: '2024-09-24', 
      category: 'regular',
      imageUrl: '/api/placeholder/400/300', //    
      description: ' CC  9  ' 
    },
    { 
      id: 6, 
      title: '2024 8  ', 
      date: '2024-08-27', 
      category: 'regular',
      imageUrl: '/api/placeholder/400/300', //    
      description: ' CC  8  ' 
    },
    { 
      id: 7, 
      title: '2024 6  ', 
      date: '2024-06-25', 
      category: 'regular',
      imageUrl: '/api/placeholder/400/300', //    
      description: ' CC  6  ' 
    },
    { 
      id: 8, 
      title: '2024 ', 
      date: '2024-05-28', 
      category: 'special',
      imageUrl: '/api/placeholder/400/300', //    
      description: '34   ' 
    },
  ];

  //   
  const filteredItems = filter === 'all' 
    ? galleryItems 
    : galleryItems.filter(item => item.category === filter);

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold text-center mb-8"> </h1>
      
      {/*  */}
      <div className="flex justify-center mb-8">
        <div className="inline-flex rounded-md shadow-sm">
          <button
            type="button"
            className={`px-4 py-2 text-sm font-medium rounded-l-lg ${
              filter === 'all' 
                ? 'bg-blue-700 text-white' 
                : 'bg-white text-gray-700 hover:bg-gray-50'
            }`}
            onClick={() => setFilter('all')}
          >
            
          </button>
          <button
            type="button"
            className={`px-4 py-2 text-sm font-medium ${
              filter === 'regular' 
                ? 'bg-blue-700 text-white' 
                : 'bg-white text-gray-700 hover:bg-gray-50'
            }`}
            onClick={() => setFilter('regular')}
          >
             
          </button>
          <button
            type="button"
            className={`px-4 py-2 text-sm font-medium rounded-r-lg ${
              filter === 'special' 
                ? 'bg-blue-700 text-white' 
                : 'bg-white text-gray-700 hover:bg-gray-50'
            }`}
            onClick={() => setFilter('special')}
          >
             
          </button>
        </div>
      </div>
      
      {/*   */}
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {filteredItems.map(item => (
          <div key={item.id} className="bg-white rounded-lg shadow-md overflow-hidden">
            <img 
              src={item.imageUrl} 
              alt={item.title} 
              className="w-full h-48 object-cover"
            />
            <div className="p-4">
              <h3 className="text-lg font-semibold mb-2">{item.title}</h3>
              <p className="text-sm text-gray-500 mb-2">{item.date}</p>
              <p className="text-sm text-gray-700">{item.description}</p>
            </div>
            <div className="px-4 pb-4">
              <button className="text-blue-600 hover:text-blue-800 text-sm">
                  &rarr;
              </button>
            </div>
          </div>
        ))}
      </div>
      
      {/*    */}
      {filteredItems.length === 0 && (
        <div className="text-center py-8">
          <p className="text-gray-500">    .</p>
        </div>
      )}
      
      {/*  (   ) */}
      <div className="flex justify-center mt-8">
        <nav className="inline-flex rounded-md shadow">
          <a
            href="#"
            className="px-3 py-2 rounded-l-md border border-gray-300 bg-white text-gray-500 hover:bg-gray-50"
          >
            
          </a>
          <a
            href="#"
            className="px-3 py-2 border-t border-b border-gray-300 bg-blue-700 text-white"
          >
            1
          </a>
          <a
            href="#"
            className="px-3 py-2 border-t border-b border-gray-300 bg-white text-gray-500 hover:bg-gray-50"
          >
            2
          </a>
          <a
            href="#"
            className="px-3 py-2 border-t border-b border-gray-300 bg-white text-gray-500 hover:bg-gray-50"
          >
            3
          </a>
          <a
            href="#"
            className="px-3 py-2 rounded-r-md border border-gray-300 bg-white text-gray-500 hover:bg-gray-50"
          >
            
          </a>
        </nav>
      </div>
    </div>
  );
};

export default GalleryPage;