import React, { useState } from 'react';

const SchedulePage = () => {
  const [selectedYear, setSelectedYear] = useState('2025');
  
  //   
  const scheduleData = {
    '2025': [
      {
        id: 1,
        date: '2025-03-25',
        formattedDate: '2025 3 25',
        title: '3  ',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 24,
        status: 'upcoming',
        description: '2025   .      .    .'
      },
      {
        id: 2,
        date: '2025-04-22',
        formattedDate: '2025 4 22',
        title: '4  ',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 28,
        status: 'upcoming',
        description: '4  .         .    .'
      },
      {
        id: 3,
        date: '2025-05-27',
        formattedDate: '2025 5 27',
        title: '5   ( )',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 32,
        status: 'upcoming',
        description: '   5  .   .      .'
      },
      {
        id: 4,
        date: '2025-06-24',
        formattedDate: '2025 6 24',
        title: '6   ( )',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 26,
        status: 'upcoming',
        description: '   .       .    .'
      },
      {
        id: 5,
        date: '2025-08-26',
        formattedDate: '2025 8 26',
        title: '8   (  )',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 0,
        status: 'upcoming',
        description: '   .      .    .'
      },
      {
        id: 6,
        date: '2025-09-23',
        formattedDate: '2025 9 23',
        title: '9  ',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 0,
        status: 'upcoming',
        description: '9  .        .    .'
      },
      {
        id: 7,
        date: '2025-10-28',
        formattedDate: '2025 10 28',
        title: '10   ( )',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 0,
        status: 'upcoming',
        description: '   10  .      .      .'
      },
      {
        id: 8,
        date: '2025-11-25',
        formattedDate: '2025 11 25',
        title: '11   ( )',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 0,
        status: 'upcoming',
        description: '2025   .     .    .'
      }
    ],
    '2024': [
      {
        id: 9,
        date: '2024-03-26',
        formattedDate: '2024 3 26',
        title: '3  ',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 24,
        status: 'completed',
        description: '2024   .      .    .'
      },
      {
        id: 10,
        date: '2024-04-23',
        formattedDate: '2024 4 23',
        title: '4  ',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 28,
        status: 'completed',
        description: '4  .     .    .'
      },
      {
        id: 11,
        date: '2024-05-28',
        formattedDate: '2024 5 28',
        title: '5   ( )',
        location: 'CC ( )',
        time: ' 11:00',
        participants: 32,
        status: 'completed',
        description: '   5  .     .      .'
      }
    ]
  };
  
  const schedules = scheduleData[selectedYear] || [];
  
  //     
  const statusStyles = {
    completed: {
      badge: 'bg-gray-200 text-gray-700',
      text: ''
    },
    upcoming: {
      badge: 'bg-blue-100 text-blue-700',
      text: ''
    },
    canceled: {
      badge: 'bg-red-100 text-red-700',
      text: ''
    }
  };
  
  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold text-center mb-8"> </h1>
      
      {/*   */}
      <div className="mb-8 flex justify-center">
        <div className="inline-flex rounded-md shadow-sm" role="group">
          {Object.keys(scheduleData).map(year => (
            <button
              key={year}
              type="button"
              className={`px-6 py-2 text-sm font-medium ${
                selectedYear === year
                  ? 'bg-blue-600 text-white'
                  : 'bg-white text-gray-700 hover:bg-gray-100'
              } border border-gray-200 ${
                year === Object.keys(scheduleData)[0] ? 'rounded-l-lg' : ''
              } ${
                year === Object.keys(scheduleData)[Object.keys(scheduleData).length - 1]
                  ? 'rounded-r-lg'
                  : ''
              }`}
              onClick={() => setSelectedYear(year)}
            >
              {year}
            </button>
          ))}
        </div>
      </div>
      
      {/*   */}
      <div className="space-y-6">
        {schedules.length > 0 ? (
          schedules.map(schedule => (
            <div key={schedule.id} className="bg-white shadow rounded-lg overflow-hidden">
              <div className="p-6">
                <div className="sm:flex sm:justify-between sm:items-center mb-4">
                  <div>
                    <h2 className="text-xl font-semibold">{schedule.title}</h2>
                    <p className="text-gray-600 mt-1">{schedule.formattedDate}</p>
                  </div>
                  <div className="mt-2 sm:mt-0">
                    <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${statusStyles[schedule.status].badge}`}>
                      {statusStyles[schedule.status].text}
                    </span>
                  </div>
                </div>
                
                <div className="border-t border-gray-200 pt-4">
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div className="flex items-center">
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 text-gray-500 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                      </svg>
                      <span className="text-gray-700">{schedule.location}</span>
                    </div>
                    <div className="flex items-center">
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 text-gray-500 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                      </svg>
                      <span className="text-gray-700">{schedule.time} </span>
                    </div>
                    <div className="flex items-center">
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 text-gray-500 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0z" />
                      </svg>
                      <span className="text-gray-700">
                        {schedule.status === 'completed' 
                          ? `${schedule.participants} ` 
                          : schedule.participants > 0 
                            ? ` ${schedule.participants} ` 
                            : '  '}
                      </span>
                    </div>
                  </div>
                </div>
                
                {schedule.description && (
                  <div className="mt-4">
                    <p className="text-gray-700">{schedule.description}</p>
                  </div>
                )}
                
                {schedule.status === 'upcoming' && (
                  <div className="mt-6">
                    <button className="inline-flex items-center justify-center px-6 py-3 border border-transparent text-base font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 md:py-2 md:text-lg md:px-5">
                       
                    </button>
                  </div>
                )}
              </div>
            </div>
          ))
        ) : (
          <div className="bg-white shadow rounded-lg p-6 text-center text-gray-500">
            <p>   .</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default SchedulePage;